<template>
    <div class="aside-container">
        <div class="aside-profile">
            <router-link to='/login'>
                <img :src='author.author_img' alt="五月的博客">
            </router-link>
            <span @click="login" class="aside-profile-title">{{author.author_name}}的博客</span>
            <span class="aside-profile-description">{{author.author_talk}}</span>
        </div>

        <button class="btn" @click="index">首页</button>
    </div>
</template>

<script>
import api from '@/api/index.js'
export default {
    data(){
        return {
            show: !this.$store.state.show,
            author:{}
        }
    },
    created(){
        api.getAuthor(1).then(res=>{
            this.author = res.data;
        })
    },
    methods: {
        login(){
            this.$router.push({path:'login'})
        },
        index(){
            this.$router.push({name: 'defaultPage'})
        }
    },
}
</script>


<style lang="scss">
    @import '@/assets/css/aside/index.scss';
</style>